import { Paper, Switch } from "@mui/material";
import React from "react";
import ProductsServices from "services/ProductsServices";
import ProductCard from "components/ProductCard";
import Product from "interfaces/Product";
import AppContext from "utils/AppContext";
import ShoppingCartServices from "services/ShoppingCartServices";
import ShoppingCart from "components/ShoppingCart";

function Main() {
  const [products, setProducts] = React.useState<Product[]>([]);
  const context = React.useContext(AppContext);

  React.useEffect(() => {
    getProducts();
    newShoppingCart();
  }, []);

  const newShoppingCart = () => {
    ShoppingCartServices.newShoppingCart().then((response) => {
      context.setShoppingCart(response);
    });
  };

  const getProducts = () => {
    ProductsServices.getProducts().then((products: Product[]) => {
      setProducts(products);
    });
  };

  return (
    <Paper square>
      <div className="min-h-screen w-screen">
        <div className="flex justify-center items-center h-32">
          Modo administrador:{" "}
          <Switch onChange={(e) => context.setAdmin(e.target.checked)} />
        </div>
        <div className="h-full w-full flex justify-center items-start flex-wrap">
          <ShoppingCart />
          {products.map((product: Product, index: number) => (
            <ProductCard key={index} product={product} />
          ))}
        </div>
      </div>
    </Paper>
  );
}

export default Main;
