import React from "react";
import AppRouter from "./routes/AppRouter";
import AppContext from "./utils/AppContext";
import { createTheme, ThemeProvider } from "@mui/material";
import ShoppingCart from "interfaces/ShoppingCart";

function App() {
  const [admin, setAdmin] = React.useState(false);
  const [shoppingCart, setShoppingCart] = React.useState<ShoppingCart>();

  const setShoppingCartState = (shoppingCart: ShoppingCart) => {
    setShoppingCart(shoppingCart);
  };

  const setAdminState = (admin: boolean) => {
    setAdmin(admin);
  };

  const context = {
    setShoppingCart: setShoppingCartState,
    shoppingCart,
    admin,
    setAdmin: setAdminState
  };

  const darkTheme = createTheme({
    palette: {
      mode: "dark",
    },
  });

  return (
    <AppContext.Provider value={context}>
      <ThemeProvider theme={darkTheme}>
        <AppRouter />
      </ThemeProvider>
    </AppContext.Provider>
  );
}

export default App;
