import axios from "axios";
import Product from "interfaces/Product";
import ShoppingCart from "interfaces/ShoppingCart";

const API_URL = process.env.REACT_APP_API_URL + "/carrito";

const newShoppingCart = async () => {
  return axios.post(API_URL).then((response) => {
    return response.data;
  });
};

const deleteProduct = async (cartId: ShoppingCart["id"], productId: Product["id"]) => {
  return axios
    .delete(`${API_URL}/${cartId}/productos/${productId}`)
    .then((response) => {
      return response.data;
    });
};

const addProduct = async (cart: ShoppingCart, product: Product) => {
  return axios
    .post(`${API_URL}/${cart.id}/productos`, product)
    .then((response) => {
      return response.data;
    });
};

const exportedMethods = {
  newShoppingCart,
  addProduct,
  deleteProduct,
};

export default exportedMethods;
