import React from "react";
import Product from "interfaces/Product";
import Card from "@mui/material/Card";
import CardActions from "@mui/material/CardActions";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";
import AppContext from "utils/AppContext";
import { TextField } from "@mui/material";
import ProductsServices from "services/ProductsServices";
import ShoppingCartServices from "services/ShoppingCartServices";

function ProductCard({ product }: { product: Product }) {
  const context = React.useContext(AppContext);
  const [editing, setEditing] = React.useState(false);
  const [productEditing, setProductEditing] = React.useState<Product>(product);

  const handleEdit = () => {
    ProductsServices.saveProduct(productEditing).then((response) => {
      setEditing(false);
      alert("Producto editado con éxito");
      window.location.reload();
    });
  };

  const handleDelete = () => {
    ProductsServices.deleteProduct(product.id).then(() => {
      setEditing(false);
      alert("Producto eliminado con éxito");
      window.location.reload();
    });
  };

  const addProductToShoppingCart = async () => {
    context.setShoppingCart({
      ...context.shoppingCart,
      products: [...context.shoppingCart.products, product],
    });

    await ShoppingCartServices.addProduct(context.shoppingCart, product);
  };

  return (
    <div className="p-4">
      <Card sx={{  maxHeight: 200, maxWidth: 200, minWidth: 200, minHeight: 450 }}>
        <CardMedia
          component="img"
          image={product.pictureUrl}
          sx={{ maxHeight: 200, maxWidth: 200, minWidth: 200, minHeight: 200 }}
        />
        {!editing && (
          <>
            <CardContent>
              <Typography gutterBottom variant="h5" component="div">
                {product.name}
              </Typography>
              <Typography variant="body1" color="text.secondary">
                Precio: {product.price}
              </Typography>
              <Typography variant="body2" color="text.secondary">
                Stock: {product.quantity}
              </Typography>
            </CardContent>
            <CardActions>
              <Button size="small" onClick={() => addProductToShoppingCart()}>Añadir al carrito</Button>
            </CardActions>
            {context.admin && (
              <CardActions>
                <Button size="small" onClick={(e) => setEditing(!editing)}>
                  Editar
                </Button>
                <Button size="small" onClick={(e) => handleDelete()}>
                  Eliminar
                </Button>
              </CardActions>
            )}
          </>
        )}
        {editing && (
          <>
            <CardContent>
              <TextField
                size="small"
                placeholder="Nombre"
                type="text"
                value={productEditing.name}
                onChange={(e) => {
                  setProductEditing({
                    ...productEditing,
                    name: e.target.value,
                  });
                }}
              />
              <TextField
                size="small"
                placeholder="Precio"
                type="text"
                value={productEditing.price}
                onChange={(e) => {
                  setProductEditing({
                    ...productEditing,
                    price: e.target.value,
                  });
                }}
              />
              <TextField
                size="small"
                placeholder="Stock"
                type="text"
                value={productEditing.quantity}
                onChange={(e) => {
                  setProductEditing({
                    ...productEditing,
                    quantity: e.target.value,
                  });
                }}
              />
              <TextField
                size="small"
                placeholder="URL de imagen"
                type="text"
                value={productEditing.pictureUrl}
                onChange={(e) => {
                  setProductEditing({
                    ...productEditing,
                    pictureUrl: e.target.value,
                  });
                }}
              />
            </CardContent>
            <CardActions>
              <Button size="small" onClick={(e) => handleEdit()}>Guardar</Button>
              <Button size="small" onClick={(e) => setEditing(!editing)}>Volver</Button>
            </CardActions>
          </>
        )}
      </Card>
    </div>
  );
}

export default ProductCard;
