import { Card, CardContent, IconButton, Typography } from "@mui/material";
import React from "react";
import AppContext from "utils/AppContext";
import Product from "interfaces/Product";
import DeleteIcon from "@mui/icons-material/Delete";
import ShoppingCartServices from "services/ShoppingCartServices";

function ShoppingCart() {
  const context = React.useContext(AppContext);

  const deleteProductFromCart = async (index: number, productId: Product["id"]) => {
    let newProductsList = [...context.shoppingCart.products];
    newProductsList.splice(index, 1);
    context.setShoppingCart({
      ...context.shoppingCart,
      products: newProductsList,
    });

    await ShoppingCartServices.deleteProduct(context.shoppingCart.id, productId);
  };

  return (
    <div className="p-4">
      <Card
        sx={{
          maxHeight: 200,
          maxWidth: 200,
          minWidth: 200,
          minHeight: 450,
        }}
      >
        <CardContent>
          <Typography gutterBottom variant="h5" component="div">
            Carrito:
          </Typography>
          {context.shoppingCart &&
            context.shoppingCart.products.map(
              (product: Product, index: number) => (
                <div key={index} className="flex flex-row items-center justify-start">
                  <Typography variant="body1" color="text.secondary">
                    {product.name}
                  </Typography>
                  <IconButton
                    color="primary"
                    onClick={() => deleteProductFromCart(index, product.id)}
                  >
                    <DeleteIcon />
                  </IconButton>
                </div>
              )
            )}
        </CardContent>
      </Card>
    </div>
  );
}

export default ShoppingCart;
